import React from "react";

function ProjectSpotlightImage(props) {
    return (
        <a className="gallery-item" href={props.imagePath}>
            <img src={props.imagePath} alt="placeholder"/>
        </a>
    )
}


function ProjectSpotlight(props) {

  return (
    <section className="project-spotlight navbar-spacing">
        <div className="container">
            <div className="row">
                <div className="col-lg-6 sidebar">
                    <div className="sidebar-content">
                        <h2>{props.project.name}</h2>
                        <div>Some date/tag alignment</div>
                        <hr />
                        <h5>Synopsis</h5>
                        {props.project.synopsis.map((descriptionLine, index)=>{
                            return <p key={index}>{descriptionLine}</p>
                        })}
                        <h5>How it works</h5>
                        <p>Some more description</p>
                        <h5>Challenge and Future work</h5>
                        <p>Yada yada yada</p>
                        <h5>Share + Links</h5>
                        <p>Some links</p>
                    </div>
                </div>
                <div className="col-lg-6 gallery">
                    <div className="gallery-container">
                        
                        {props.project.images.map((imagePath, index) => {
                            console.log(imagePath);
                            return <ProjectSpotlightImage key={index} imagePath={imagePath} />
                        })}

                    </div>
                </div>
            </div>
        </div>
    </section>
  );
}

export default ProjectSpotlight;